@keyframes humburger-top-line {
  0% {
    transform: rotate(0) translateY(0);
  }
  50% {
    transform: rotate(0deg) translate(5px, 4px);
  }
  100% {
    transform: rotate(45deg) translate(5px, 4px);
  }
}
@keyframes humburger-bottom-line {
  0% {
    transform: rotate(0) translateY(0);
  }
  50% {
    transform: rotate(0) translate(7px, -7px);
  }
  100% {
    transform: rotate(-45deg) translate(7px, -7px);
  }
}
@keyframes fide {
  0% {
    transform: translateY(-25%);
    opacity: 0;
  }
  25% {
    opacity: 0.5;
  }
  50% {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes downloud-window {
  0% {
    opacity: 0;
    transform: translate(0%, 100px);
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
    transform: translate(0%, 0%);
  }
}
@keyframes default-animate {
  0% {
    transform: translateY(100px);
    opacity: 0;
  }
  50% {
    opacity: 0.5;
    visibility: visible;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
